import React from 'react';
import { HashRouter as Router, Route, Routes } from 'react-router-dom'; // Usa HashRouter
import Home from './components/Home';
import EcoRuta from './Proyects/EcoRuta';
import Momentos from './Proyects/Momentos';
import CMV from './Proyects/CMV';

function App() {
  return (
    <Router basename="/Portafolio-ThonyMarckDEV">
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/EcoRuta" element={<EcoRuta />} />
        <Route path="/momentos" element={<Momentos />} />
        <Route path="/cmv" element={<CMV />} />
      </Routes>
    </Router>
  );
}

export default App;
